@import url(https://fonts.googleapis.com/css?family=Source+Sans+Pro:regular,bold,italic&subset=latin,latin-ext);
:root {
  --red: #ff5757;
  --lightred: #e57e7e;
  --darkred: #bc4141;
  --darkgray: #545454;
  --lightgray: #c7d0d8;
  --beige: #f7d29a;
  --blue: #0f9ec4;
  --yellow: #ffbd59;
}

body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-family: 'Source Sans Pro', sans-serif;
}

.topnav {
  width: 100%;
  height: 50px;
  background-color: var(--darkgray);
}

@media screen and (min-width: 640px) {
  .topnav {
    height: 65px;
  }

  .topnav img {
    width: 170px;
  }
}

@media screen and (min-width: 1024px) {
  .topnav {
    height: 75px;
  }

  .topnav img {
    width: 200px;
  }
}

.intro {
  background-color: var(--red);
  border-bottom: 3px solid var(--darkgray);
  margin-bottom: 15px;
  padding: 15px;
}

.intro p {
  padding: 15px 10px;
  margin: 0 auto;
  color: white;
  font-size: 24px;
}

.homelink {
  max-width: 1200px;
  margin: 0 auto 15px;
  padding: 15px;
  background-color: var(--beige);
}

.homelink h4 {
  font-size: 24px;
  letter-spacing: 1.2px;
}

.homelink .form {
  position: relative;
}

.homelink .icon {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border-radius: 100%;
  z-index: 2;
}

.homelink.create .icon {
  background-color: var(--red);
}

.homelink.find .icon {
  background-color: var(--blue);
}

.homelink input {
  margin-bottom: 20px;
  margin-left: 45px;
  padding-left: 20px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--darkgray);
  font-size: 20px;
  width: calc(100% - 80px);
}

.homelink input::-webkit-input-placeholder {
  font-family: 'Source Sans Pro';
  font-size: 20px;
}

.homelink input::-moz-placeholder {
  font-family: 'Source Sans Pro';
  font-size: 20px;
}

.homelink input:-ms-input-placeholder {
  font-family: 'Source Sans Pro';
  font-size: 20px;
}

.homelink input::-ms-input-placeholder {
  font-family: 'Source Sans Pro';
  font-size: 20px;
}

.homelink input::placeholder {
  font-family: 'Source Sans Pro';
  font-size: 20px;
}

.homelink .circleIcon {
  font-size: 20px;
}

.homelink .link.disabled {
  opacity: 0.5;
  pointer-events: none;
}

.homelink .arrow {
  font-size: 30px;
  padding: 15px;
}

.homelink.create .arrow path {
  fill: var(--red);
}

.homelink.find .arrow path {
  fill: var(--blue);
}

.homelink .form a {
  position: absolute;
  right: 0;
  bottom: 10px;
}

@media screen and (min-width: 640px) {
  .intro p {
    max-width: 1200px;
    font-size: 45px;
    padding-top: 25px;
    padding-bottom: 25px;
  }

  .homelink {
    padding: 25px;
  }

  .homelink h4 {
    font-size: 30px;
  }

  .homelink .form {
    width: 50%;
  }

  .homelink .icon {
    width: 60px;
    height: 60px;
  }

  .homelink .circleIcon {
    font-size: 25px;
  }

  .homelink input {
    font-size: 30px;
    padding-left: 30px;
    padding-bottom: 10px;
  }

  .homelink input::-webkit-input-placeholder {
    font-size: 30px;
  }

  .homelink input::-moz-placeholder {
    font-size: 30px;
  }

  .homelink input:-ms-input-placeholder {
    font-size: 30px;
  }

  .homelink input::-ms-input-placeholder {
    font-size: 30px;
  }

  .homelink input::placeholder {
    font-size: 30px;
  }

  .homelink .arrow {
    font-size: 40px;
    padding: 15px;
  }
}

@media screen and (min-width: 1024px) {
  .homelink {
    padding-left: 80px;
    padding-bottom: 60px;
  }
}

.select {
  max-width: 1000px;
  margin: 0 auto;
  text-align: center;
}

.select .select-for {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 0;
}

.select .event-name {
  font-size: 30px;
  margin-top: 0;
}

.select .options {
  padding: 0;
  border: 3px solid var(--lightred);
}

.select .options button {
  width: 100%;
  padding-top: 10px;
  padding-bottom: 10px;
  cursor: pointer;
  font-weight: bold;
  font-size: 18px;
  border: none;
  border-bottom: 3px solid var(--lightred);
}

.select .options button:last-child {
  border-bottom: none;
}

.select .options button.selected {
  background-color: var(--lightred);
  color: white;
}

.select button.done {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: var(--red);
  color: white;
  font-weight: bold;
  font-size: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  border: none;
  opacity: 0.5;
}

.select button.done.active {
  cursor: pointer;
  opacity: 1;
}

.select .input {
  width: 100%;
  display: flex;
  flex-direction: column;
  text-align: left;
  margin-bottom: 30px;
}

.select label {
  color: gray;
  font-size: 16px;
}

.select input {
  background-color: white;
  border: 2px solid var(--yellow);
  padding-top: 10px;
  padding-bottom: 10px;
  font-family: 'Source Sans Pro', sans-serif;
  font-size: 18px;
  font-weight: bold;
  border-radius: 5px;
}

.days-of-week {
  margin: 20px auto 30px;
  display: inline-block;
}

.day-of-week {
  text-align: left;
  margin-bottom: 15px;
}

.day-of-week label {
  font-size: 20px;
  font-weight: bold;
}

@media screen and (min-width: 640px) {
  .select button.done {
    position: relative;
  }
}

.DayPicker {
  margin-bottom: 55px;
}

.range-picker .DayPicker-Day--selected:not(.DayPicker-Day--start):not(.DayPicker-Day--end):not(.DayPicker-Day--outside) {
  background-color: #f0f8ff !important;
  color: #4a90e2;
}

.specific-picker .DayPicker-Day--selected:not(.DayPicker-Day--outside) {
  background-color: var(--yellow) !important;
  color: white !important;
  font-weight: bold !important;
  border-radius: 0 !important;
}

.range-picker .DayPicker-Day {
  border-radius: 0 !important;
}
.range-picker .DayPicker-Day--start {
  border-top-left-radius: 50% !important;
  border-bottom-left-radius: 50% !important;
}
.range-picker .DayPicker-Day--end {
  border-top-right-radius: 50% !important;
  border-bottom-right-radius: 50% !important;
}

.code-copy {
  align-self: flex-start;
  display: flex;
  align-items: center;
  width: 100%;
  margin-bottom: 10px;
}

.code-copy .label {
  width: 30%;
  white-space: nowrap;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  text-align: right;
  color: var(--darkgray);
  margin-right: 10px;
}

.code-copy .clipboard-code {
  text-align: left;
  background-color: var(--lightgray);
  color: var(--darkgray);
  font-family: 'Source Sans Pro', sans-serif;
  border-radius: 2px;
  white-space: nowrap;
  font-size: 12px;
  text-transform: uppercase;
  font-weight: bold;
  border: none;
  padding: 5px 10px;
  cursor: pointer;
}

.code-copy .clipboard {
  margin-left: 5px;
}

@media screen and (min-width: 640px) {
  .code-copy {
    width: auto;
    align-self: auto;
    margin-bottom: 0;
    margin-right: 25px;
  }

  .code-copy .label {
    width: auto;
  }
}

.user-option {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  background-color: transparent;
  border: none;
  width: 100%;
  text-align: left;
  padding: 15px 20px;
  cursor: pointer;
}

.user-option.existing {
  padding: 10px 20px 10px 35px;
}

.user-option.selected {
  background-color: var(--lightred);
  color: white;
  font-weight: bold;
}

.user-option .icon {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  z-index: 2;
}

.user-option.new .icon {
  width: 60px;
  height: 60px;
}

.user-option.new .circleIcon {
  font-size: 20px;
}

.user-option.existing .icon {
  width: 40px;
  height: 40px;
}

.user-option.existing .circleIcon {
  font-size: 16px;
}

.user-option:nth-of-type(4n+1) .icon {
  background-color: var(--blue);
}

.user-option:nth-of-type(4n+2) .icon {
  background-color: var(--darkgray);
}

.user-option:nth-of-type(4n+3) .icon {
  background-color: var(--yellow);
}

.user-option:nth-of-type(4n+0) .icon {
  background-color: var(--red);
}

.user-option input {
  margin-left: -5px;
  padding-left: 30px;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid var(--darkgray);
  font-size: 20px;
  width: 100%;
}

.user-option.selected input {
  color: white;
  font-weight: bold;
  border-bottom: 1px solid white;
}

.user-option input::-webkit-input-placeholder {
  font-family: 'Source Sans Pro';
  font-size: 22px;
}

.user-option input::-moz-placeholder {
  font-family: 'Source Sans Pro';
  font-size: 22px;
}

.user-option input:-ms-input-placeholder {
  font-family: 'Source Sans Pro';
  font-size: 22px;
}

.user-option input::-ms-input-placeholder {
  font-family: 'Source Sans Pro';
  font-size: 22px;
}

.user-option input::placeholder {
  font-family: 'Source Sans Pro';
  font-size: 22px;
}

.user-option.selected input::-webkit-input-placeholder {
  color: white;
  font-family: 'Source Sans Pro';
  font-size: 22px;
  font-weight: normal;
}

.user-option.selected input::-moz-placeholder {
  color: white;
  font-family: 'Source Sans Pro';
  font-size: 22px;
  font-weight: normal;
}

.user-option.selected input:-ms-input-placeholder {
  color: white;
  font-family: 'Source Sans Pro';
  font-size: 22px;
  font-weight: normal;
}

.user-option.selected input::-ms-input-placeholder {
  color: white;
  font-family: 'Source Sans Pro';
  font-size: 22px;
  font-weight: normal;
}

.user-option.selected input::placeholder {
  color: white;
  font-family: 'Source Sans Pro';
  font-size: 22px;
  font-weight: normal;
}

.user-option .existing-name {
  margin-left: 20px;
  font-size: 18px;
}

.user-option .new-name {
  width: calc(100% - 150px);
}

.select-user {
  max-width: 750px;
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.select-user h4 {
  font-size: 20px;
  letter-spacing: 1px;
}

.select-user button.done {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background-color: var(--red);
  color: white;
  font-weight: bold;
  font-size: 20px;
  padding-top: 10px;
  padding-bottom: 10px;
  border: none;
  opacity: 0.5;
}

.select-user button.done.active {
  opacity: 1;
  cursor: pointer;
}

@media screen and (min-width: 640px) {
  .select-user h4 {
    font-size: 22px;
    letter-spacing: 1px;
  }

  .select-user .options {
    margin-bottom: 50px;
  }

  .select-user button.done {
    position: relative;
  }
}

.day {
  margin-right: 30px;
}

.day h3 {
  text-transform: uppercase;
  letter-spacing: 1.5px;
  font-size: 18px;
  margin-bottom: 3px;
  margin-left: 40px;
}

.day .header-line {
  border-color: var(--darkgray);
  border-width: 1.5px;
  border-style: solid;
}

.day .timeslot {
  display: flex;
  align-items: center;
  margin-left: 20px;
  margin-bottom: 10px;
  position: relative;
}

.day .timeslot p {
  font-size: 18px;
  margin: 0;
  white-space: nowrap;
}

.day .timeslot button {
  border: none;
  width: 100%;
  padding: 9px;
  margin-left: 10px;
}

.day .timeslot.has-tooltip button {
  cursor: pointer;
}

.day .timeslot button.selected {
  background-color: var(--blue);
}

.day .timeslot button.unselected {
  background-color: lightgray;
}

.day .timeslot.overlap button {
  color: black;
  font-weight: bolder;
  font-size: 16px;
}

.day .timeslot.overlap .tooltip {
  display: none;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  position: absolute;
  right: 15px;
  width: calc(100% - 147px);
  top: 50%;
  background-color: var(--yellow);
  padding: 20px;
  z-index: 2;
  box-shadow: 0 0 5px rgba(0,0,0,0.5);
  font-weight: bold;
  font-size: 20px;
  color: var(--darkgray);
}

.day .timeslot.overlap .tooltip.open {
  display: flex;
}

.day .tooltip-user {
  display: flex;
  align-items: center;
  margin-bottom: 10px;
}

.day .timeslot.overlap .icon {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  width: 30px;
  height: 30px;
  font-size: 16px;
  margin-right: 15px;
}

.day .tooltip-user .icon.red {
  background-color: var(--red);
}

.day .tooltip-user .icon.blue  {
  background-color: var(--blue);
}

.day .tooltip-user .icon.darkgray  {
  background-color: var(--darkgray);
}

.day .timeslot.overlap .tooltip .close-button {
  position: absolute;
  top: 0;
  right: 0;
  height: 40px;
  width: 40px;
  font-size: 22px;
  background-color: transparent;
}

@media screen and (min-width: 640px) {
  .schedule {
    display: flex;
    flex-wrap: wrap;
  }

  .day .timeslot {
    width: 200px;
  }

  .day .timeslot.overlap .tooltip {
    left: auto;
    right: -20px;
    top: calc(100% - 15px);
    min-width: 120px;
    width: auto;
    min-height: 70px;
  }
}

@media screen and (min-width: 1024px) {
  .day .timeslot.overlap .tooltip {
    left: calc(100% - 15px);
    right: auto;
    top: calc(100% - 15px);
  }

  .day .timeslot.overlap:hover .tooltip {
    display: flex;
  }

  .day .timeslot.overlap .tooltip .close-button {
    display: none;
  }
}

.user-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 20px;
  margin-top: 20px;
}

.user-list .user {
  display: flex;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: var(--darkgray);
  margin-right: 20px;
  margin-bottom: 20px;
}

.user-list .icon {
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 100%;
  width: 30px;
  height: 30px;
  font-size: 16px;
  margin-right: 15px;
}

.user-list .user:nth-child(3n + 1) .icon {
  background-color: var(--red);
}

.user-list .user:nth-child(3n + 2) .icon {
  background-color: var(--blue);
}

.user-list .user:nth-child(3n + 0) .icon {
  background-color: var(--darkgray);
}

@media screen and (min-width: 640px) {
  .user-list {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: flex-start;
    align-items: center;
  }
}

.calendar .header {
  display: flex;
  flex-direction: column;
}

.calendar h1 {
  align-self:  center;
  font-size: 22px;
  color: var(--darkgray);
  font-weight: normal;
}

.calendar .tabs {
  display: flex;
}

.calendar .tabs button {
  flex-basis: 50%;
  padding: 8px;
  background-color: var(--lightred);
  color: var(--darkred);
  text-transform: uppercase;
  font-weight: bold;
  border: none;
  border-bottom: 5px solid var(--red);
  cursor: pointer;
}

.calendar .tabs button.selected {
  background-color: var(--red);
  color: white;
}

@media screen and (min-width: 640px) {
  .calendar .header {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
  }

  .calendar .header h1 {
    width: 50%;
    text-align: center;
    font-size: 30px;
  }
}

@media screen and (min-width: 1024px) {
  .calendar .tabs button {
    font-size: 20px;
  }
}

